<template>
    <div class='querendingdan'>
        <div class="dizhi">
            <h1>{{$t('querendingdan[0]')}}</h1>
            <div class="dizhiList" v-if="address_list.length > 0">
                <div v-for="(item,i) in address_list" :key="i" @click="xuanzhong(i)" class="dizhiItem" :class="{checked:num == i}">
                    <div>
                        <div>{{item.name}}</div>
                        <div class="moren" v-if="item.status == 1">{{$t('querendingdan[1]')}}</div>
                        <div class="moren" v-else></div>
                    </div>
                    <div>
                        {{item.province}}{{item.city}}{{item.area}}{{item.address}}
                    </div>
                    <div><span @click="xiugaidizhi()">{{$t('querendingdan[2]')}}</span></div>
                </div>
            </div>
            <div class="dizhiList" v-else>
                <div class="dizhiItem checked" style="font-size: 22px; text-align: center;line-height: 100px;cursor:pointer;" @click="xiugaidizhi()">
                    请添加收货地址
                </div>
            </div>
            <div class="dizhiguanli">
                <span @click="xiugaidizhi()">{{$t('querendingdan[3]')}}</span>
            </div>
        </div>
        <div class="xinxi">
            <h1>{{$t('querendingdan[4]')}}</h1>
            <div class="xinxiTitle">
                <div>{{$t('querendingdan[5]')}}</div>
                <div>商品属性</div>
                <div>{{$t('querendingdan[6]')}}</div>
                <div>{{$t('querendingdan[7]')}}</div>
                <div></div>
                <div>{{$t('querendingdan[8]')}}</div>
            </div>
            <!-- <p>店铺：店铺名字</p> -->
            <div class="goodsList">
                <div class="proXinxi" v-if="goodsList" v-for="(item,i) in goodsList.goods_list" :key="i">
                    <div>
                        <div class="img">
                            <img :src="imgUrl + item.thumb" alt="">
                        </div>
                        <div class="text" v-if="apis.lang">{{item.name}}</div>
                        <div class="text" v-else>{{item.english_name}}</div>
                    </div>
                    <div class="shuxing">{{item.attr}}</div>
                    <div class="price"><span>￥</span><span>{{item.sell_price}}</span></div>
                    <div class="proNum">{{item.num}}</div>
                    <div class="youhui"></div>
                    <!-- <div class="youhui">-<span>￥</span><span>235.00</span></div> -->
                    <div class="zongji">￥{{item.sell_price * item.num}}</div>
                    <div class="hr"></div>
                </div>
            </div>
            <div class="xiadanXinxi">
                <div>
                    <!-- <span>合计</span>
                    <span>￥235.00</span>
                    <span></span> -->
                </div>
                <div>
                    <div class="d1">
                        <span>{{$t('querendingdan[9]')}}：</span>
                        <span>￥</span>
                        <span>{{goodsList.price}}</span>
                    </div>
                    <div class="d2">{{$t('querendingdan[10]')}}：{{active.province}}{{active.city}}{{active.area}}{{active.address}}</div>
                    <div class="d3">{{$t('querendingdan[11]')}}: {{active.name}} {{active.phone}}</div>
                </div>
                <div>
                    <!-- <div class="btn" @click="$router.push({path:'dingdan',query:{title:'商家名字'}})">提交订单</div> -->
                    <div class="btn" @click="tijiao()">{{$t('querendingdan[12]')}}</div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            imgUrl:'',
            address_list:'',//地址列表
            get_goods_info:'',//商品详情
            goodsList:'',//商品列表
            active:'',//已选中的地址
            num:'',
        };
    },
    created:function(){
    },
    mounted:function(){
        var _this = this;
        _this.imgUrl = _this.apis.second;
        _this.apis.post(_this.apis.order,{lang:_this.apis.lang,data:_this.$route.query.data}).then(function(res) {
            if(res.data.code == 0){
                _this.goodsList = res.data.data
            }
        })
        _this.getAddressList();
        _this.getPro();
    },
    watch:{
    },
    computed:{
        zongji:function(){
            var _this = this;
            return _this.$route.query.num*_this.get_goods_info.sell_price
        }
    },
    methods:{
        //获取地址列表
        getAddressList(){
            var _this = this;
            _this.apis.get(_this.apis.address_list,{params:{lang:_this.apis.lang}}).then(function(res) {
                var arr = [];
                if(res.data.code == 0 ){
                    for(let i=0;i<res.data.data.data.length;i++){
                        console.log(res.data.data.data[i].status)
                        if(res.data.data.data[i].status == 1){
                            _this.num = i
                            _this.active = res.data.data.data[i]
                        }
                    }
                    _this.address_list = res.data.data.data
                }
            })
        },
        //获取商品详情
        getPro(){
            var _this = this;
            _this.apis.get(_this.apis.get_goods_info,{params:{lang:_this.apis.lang,id:_this.$route.query.id}}).then(function(res) {
                if(res.data.code == 0){
                    _this.get_goods_info = res.data.data
                }
            })

        },
        //选中
        xuanzhong(num){
            var _this = this
            _this.num = num
            _this.active = _this.address_list[num]
        },
        //提交订单
        tijiao(){
            var _this = this;
            _this.apis.post(_this.apis.create_order,{address_id:_this.active.id,data:_this.$route.query.data}).then(function(res) {
                if(res.data.code == 0 ){
                    _this.$router.push({path:'dingdan',query:{title:'订单',id:res.data.data.order_id}})
                }
            })
        },
        //修改地址
        xiugaidizhi(){
            var _this = this;
            _this.$router.push({path:'dizhiguanli',query:{title:'地址管理'}})
        }
    },
    components:{
    }
}
</script>
<style lang='less' scoped>
.querendingdan{
    width: 1200px;
    margin: 0 auto;
    .dizhi{
        h1{
            color: #2D2D2D;
            font-size: 15px;
            margin-bottom: 10px;
        }
        .dizhiList{
            width: 1200px;
            max-height: 314px;
            align-items: top;
            overflow-y: scroll;
            margin-bottom: 50px;
            .checked{
                background: #FFE9DF;
                border: 2px solid #FFC777 !important;
                &>div:nth-child(3){
                    display: block !important;
                }
            }
            .dizhiItem:hover{
                background: #FFE9DF;
                border: 2px solid #FFC777 !important;
            }
            .dizhiItem{
                display: inline-block;
                width: 284px;
                height: 120px;
                padding: 15px 20px;
                padding-top: 10px;
                margin-bottom: 10px;
                margin-right: 10px;
                border: 1px solid #D1D0D0;
                position: relative;
                font-weight: bold;
                color: #707070;
                &>div:nth-child(1){
                    font-size: 14px;
                    display: flex;
                    justify-content: space-between;
                    align-items: top;
                    margin-bottom: 10px;
                    .moren{
                        color: #FF8A00;
                        font-size: 12px;
                    }
                }
                &>div:nth-child(2){

                }
                &>div:nth-child(3){
                    display: none;
                    position: absolute;
                    left: 20px;
                    bottom: 9px;
                    span{
                        color: #FF8A00;
                        cursor:pointer;
                    }
                }
            }
        }
        .dizhiguanli{
            margin-bottom: 70px;
            padding-bottom: 15px;
            text-align: right;
            border-bottom: 1px solid #DEDEDE;
            span{
                color: #FF8A00;
                font-size: 13px;
                font-weight: bold;
                cursor:pointer;
            }
        }
    }
    .goodsList{
        .proXinxi{
            border-bottom: 1px solid #efefef;
        }
        &>div:last-child{
            border-bottom: none;
        }
    }
    .xinxi{
        h1{
            color: #2D2D2D;
            font-size: 15px;
            margin-bottom: 10px;
        }
        &>p{
            margin-top: 35px;
            margin-bottom: 10px;
            color: #4A4A4A;
            font-size: 13px;
            font-weight: bold;
        }
        .xinxiTitle{
            padding: 0 10px;
            margin-bottom: 35px;
            display: flex;
            justify-content: space-between;
            border-bottom: 2px solid #FFAD01;
            &>div{
                font-size: 13px;
                color: #757575;
                text-align: center;
            }
            &>div:nth-child(1){
                width: 260px;
            }
            &>div:nth-child(2){
                width: 180px;
            }
            &>div:nth-child(3){
                width: 160px;
            }
            &>div:nth-child(4){
                width: 140px;
            }
            &>div:nth-child(5){
                width: 200px;
            }
            &>div:nth-child(6){
                flex: 1;
            }
        }
        .hr:last-child{
            display: none;
        }
        &>div:nth-child(3){
            border-top: 1px solid #DEDEDE;
        }
        .proXinxi{
            display: flex;
            justify-content: space-between;
            align-items: top;
            padding: 10px 0px;
            margin: 0 10px;
            font-weight: bold;
            position: relative;
            .hr{
                position: absolute;
                bottom: 0;
                width: 95%;
                height: 1px;
                background: #DEDEDE;
            }
            &>div:nth-child(1){
                width: 260px;
                display: flex;
                justify-content: space-between;
                .img{
                    width: 62px;
                    height: 62px;
                    margin: 0 auto;
                }
                .text{
                    flex: 1;
                    height: 38px;
                    margin-left: 7px;
                    -webkit-line-clamp:2;
                    -webkit-box-orient: vertical;
                    display: -webkit-box;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    color: #4A4A4A;
                    font-size: 13px;
                }
            }
            &>div:nth-child(2){
                width: 180px;text-align: center;
                color: #6D6C6C;
                font-size: 13px;
                img{
                    width: 55px;
                    height: 55px;
                    margin-left: 5px;
                }
            }
            &>div:nth-child(3){
                width: 160px;text-align: center;
                color: #6D6C6C;
            }
            &>div:nth-child(4){
                width: 140px;text-align: center;color: #6D6C6C;
            }
            &>div:nth-child(5){
                width: 200px;text-align: center;color: #FF6508;
            }
            &>div:nth-child(6){
                flex: 1;text-align: center;color: #FF6508;font-size: 18px;
            }
        }
        .xiadanXinxi{
            border-top: 1px solid #DEDEDE;
            padding-top: 20px;
            text-align: right;
            &>div:nth-child(1){
                font-weight: bold;
                text-align: right;
                &>span:nth-child(1){
                    color: #3B3B3B;
                    font-size: 15px;
                }
                &>span:nth-child(2){
                    color: #FF6508;
                    font-size: 18px;
                }
            }
            &>div:nth-child(2){
                width: 480px;
                height: 140px;
                display: inline-block;
                font-weight: bold;
                padding: 15px;
                border: 2px solid #FFAD77;
                .d1{
                    margin-bottom: 20px;
                    &>span:nth-child(1){
                        color: #474747;
                        font-size: 13px;
                    }
                    &>span:nth-child(2){
                        color: #FF6508;
                        font-size: 20px;
                    }
                    &>span:nth-child(3){
                        color: #FF6508;
                        font-size: 28px;
                    }
                }
                .d2{
                    color: #707070;
                    font-size: 12px;
                    margin-bottom: 8px;
                }
                .d3{
                    color: #707070;
                    font-size: 14px;
                    margin-bottom: 8px;
                }
            }  
            &>div:nth-child(3){
                margin-top: 10px;
                margin-bottom: 150px;
                .btn{ 
                    cursor:pointer; 
                    display: inline-block;
                    width: 184px;
                    height: 42px;
                    text-align: center;
                    line-height: 42px;
                    background: #FFAD01;
                    color: #fff;
                    font-size: 16px;
                }
            }
        }
    }
}
</style>


